import React from 'react'
import { Box, makeStyles, Grid,  Dialog, DialogTitle, DialogContent, IconButton, Button, DialogActions, Typography } from "@material-ui/core"
import { Close as CloseIcon } from "@material-ui/icons"
import {format} from 'date-fns'

const useStyles = makeStyles(theme=>({
    skillChip:{
        margin: theme.spacing(0.5),
        padding: theme.spacing(0.75),
        fontSize : '14.5px',
        borderRadius: '5px',
        transition: '.3s',
        minHeight: '10px',
        fontWeight: 600,
        backgroundColor: theme.palette.secondary.main,
        color: '#fff'
    },
        info:{
        '& > *':{
            margin: "4px"
        }
    }
}))
export default props=>{
    const classes = useStyles();
    return(
    <Dialog open={!!Object.keys(props.job).length} fullWidth>
          <DialogTitle>
                <Box display='flex' justifyContent='space-between'>
                    {props.job.title}@{props.job.companyName}
                    <IconButton onClick={props.closeModal} ><CloseIcon /> </IconButton>
                </Box>
            </DialogTitle>
            <DialogContent>
                <Box>
                    <Box className={classes.info} display='flex' >
                        <Typography variant='caption'>Posted on:</Typography>
                        <Typography variant='body2'>{props.job.postedOn && format(props.job.postedOn,'dd/MM/yyyy HH:MM')}</Typography>
                    </Box>
                    <Box className={classes.info} display='flex' >
                        <Typography variant='caption'>Job type:</Typography>
                        <Typography variant='body2'>{props.job.type}</Typography>
                    </Box>
                    <Box className={classes.info} display='flex' >
                        <Typography variant='caption'>Job location:</Typography>
                        <Typography variant='body2'>{props.job.location}</Typography>
                    </Box>
                    <Box className={classes.info} display='flex' >
                        <Typography variant='caption'>Job description:</Typography>
                        <Typography variant='body2'>{props.job.description}</Typography>
                    </Box>
                    <Box className={classes.info} display='flex' >
                        <Typography variant='caption'>Company name:</Typography>
                        <Typography variant='body2'>{props.job.companyName}</Typography>
                    </Box>
                    <Box className={classes.info} display='flex' >
                        <Typography variant='caption'>Company website:</Typography>
                        <Typography variant='body2'>{props.job.link}</Typography>
                    </Box>
                    <Box ml={0.5} display='flex' flexDirection='column'>
                        <Typography variant='caption'>Skills:</Typography>
                        <Grid container alignItems='center'>{props.job.skills && 
                        props.job.skills.map(skill => (
                        <Grid key={skill} className={classes.skillChip}>{skill}</Grid>
                        ))}
                        </Grid>
                    </Box>
                </Box>
            </DialogContent>
            <DialogActions>
                <Button variant='outlined' component='a' href={props.job.link} target='_blank'>Apply</Button>
            </DialogActions>
    </Dialog>
)}