import React, { useState } from 'react'
import {Box, CircularProgress, Button , Select, MenuItem, makeStyles} from '@material-ui/core'

const useStyles = makeStyles({
    wrapper:{
        position:"sticky", top:90, zIndex:4,
        backgroundColor: '#fff',
        display: "flex",
        boxShadow: "0px 1px 5px rgba(0,0,0,1.1)",
        borderRadius: "5px",
        '& > *':{
            flex:1,
            height: "35px",
            margin: "8px"
        },
    },
})

export default props =>{
    const [loading, setLoading]= useState(false)
    const [jobSearch, setJobSearch] = useState({
        type:'Full time',
        location: 'Remote'
    })

      
    const handleChange = e => {
        e.persist();
        setJobSearch(oldState => ({ ...oldState, [e.target.name]: e.target.value }))
    }
    const search = async ()=>{
        setLoading(true);
        await props.fetchJobCustom(jobSearch);
        setLoading(false);
    }
 
    const classes = useStyles();
    return(
<Box p={2} mt={2} mb={2} className={classes.wrapper}>
    <Select value={jobSearch.type} variant='filled' onChange={handleChange} disableUnderline name= 'type'>
        <MenuItem  value='Full time'>Full time</MenuItem>
        <MenuItem  value='Part time'>Part time</MenuItem>
        <MenuItem value=' Contract'> Contract</MenuItem>
    </Select>

    <Select  value={jobSearch.location} name="location" variant='filled' onChange={handleChange} disableUnderline>
        <MenuItem  value='Remote'>Remote</MenuItem>
        <MenuItem  value='On Site'>On Site</MenuItem>
    </Select>
    <Button onClick={search} disabled={loading} variant='contained' color='primary' disableElevation> 
                   {loading ?<CircularProgress color='secondary' size={22}/>
                       : "Filter"
                    }</Button>
</Box>

    )
}