import React, { useState, useEffect } from "react";
import { Box, Button, CircularProgress, Grid, ThemeProvider } from "@material-ui/core";
import Header from "./components/Header"
import theme from "./theme/theme"
import Searchbar from "./components/searchbar"
import JobCard from "./components/Job/jobCard";
import NewJobModal from "./components/Job/NewJobModal";
import { firestore, app } from './firebase/config'
import { Close as CloseIcon } from "@material-ui/icons"
import ViewJobModal from "./components/Job/viewJobModal";
export default () => {
  const [viewJob, setViewJob] = useState({});
  const [jobs, setJobs] = useState([]);
  const [loading, setLoading] = useState(true);
  const [customeSearch, setcustomeSearch] = useState(false);
  const [jobModal, setJobModal] = useState(false);

  const fetchJobs = async () => {
    setcustomeSearch(false);
    setLoading(true)
    const req = await firestore.collection('jobs').orderBy('postedOn', 'desc').get();
    // console.log(req);
    const tempJobs = req.docs.map((job) => ({ ...job.data(), id: job.id, postedOn: job.data().postedOn.toDate() }));
    setJobs(tempJobs)
    setLoading(false)
  }

  const fetchJobCustom = async jobSearch => {
    setLoading(true)
    setcustomeSearch(true);
    const req = await firestore.collection('jobs').orderBy('postedOn', 'desc')
      .where("location", '==', jobSearch.location)
      .where("type", '==', jobSearch.type)
      .get();
    // console.log(req);
    const tempJobs = req.docs.map((job) => ({ ...job.data(), id: job.id, postedOn: job.data().postedOn.toDate() }));
    setJobs(tempJobs)
    setLoading(false)
  }
  const postJob = async jobDetails => {
    await firestore.collection('jobs').add({ ...jobDetails, postedOn: app.firestore.FieldValue.serverTimestamp() })
    fetchJobs();
  }

  useEffect(() => {
    fetchJobs();
  }, [])
  return <ThemeProvider theme={theme}>
    <Header openJobModal={() => setJobModal(true)} />
    <NewJobModal closeModal={() => setJobModal(false)} jobModal={jobModal} postJob={postJob} />
    <ViewJobModal job={viewJob} closeModal={()=>setViewJob({})} />
    <Box mb={3}>
      <Grid container justify='center'>
        <Grid item xs={10}>
          <Searchbar fetchJobCustom={fetchJobCustom} />
          {
            loading ?
              (<Box display='flex' justifyContent='center'>
                <CircularProgress />
              </Box>
              ) :
              (<>
                {customeSearch && (
                <Box display="flex" justifyContent="flex-end" my={2}>
                  <Button onClick={fetchJobs}>
                    <CloseIcon size={20} />
                    Custome Search
                  </Button>
                </Box>)}
                {jobs.map((job) => (<JobCard open={()=> setViewJob(job)} key={job.id} {...job} />))}
              </>
              )}


        </Grid>
      </Grid>
    </Box>
  </ThemeProvider>
};
