import React, { useState } from "react";
import { Box, makeStyles, Grid, FilledInput, Select, MenuItem, Dialog, DialogTitle, DialogContent, IconButton, Button, DialogActions, Typography, CircularProgress } from "@material-ui/core"
import { Close as CloseIcon } from "@material-ui/icons"



const useStyles = makeStyles(theme => ({
    skillChip: {
        margin: theme.spacing(0.5),
        padding: theme.spacing(0.75),
        fontSize: '14.5px',
        borderRadius: '5px',
        transition: '.3s',
        minHeight: '10px',
        fontWeight: 600,
        border: `1px solid ${theme.palette.secondary.main}`,
        color: theme.palette.secondary.main,
        cursor: 'pointer',

        '&:hover': {
            backgroundColor: theme.palette.secondary.main,
            color: "#fff",
        }
    },
    included:{
        backgroundColor: theme.palette.secondary.main,
        color: "#fff",
    }
}))
const inState = {
    companyName: "",
    companyUrl: "",
    link: "",
    location: "Remote",
    description: "",
    skills: [],
    title: "",
    type: "Full time",
    
}
export default props => {
    const [loading, setLoading] = useState(false);
    const [jobDetails, setJobDetails] = useState(inState);
    
    const handleChange = e => {
        e.persist();
        setJobDetails(oldState => ({ ...oldState, [e.target.name]: e.target.value }))
    }

   const closeModal = ()   =>{
    setJobDetails(inState);
    setLoading(false);
    props.closeModal();
   }
    const addRemoveSkill = skill => {
        jobDetails.skills.includes(skill)
            ? setJobDetails(oldState => ({ ...oldState, skills: oldState.skills.filter(s => s !== skill) }))
            : setJobDetails(oldState => ({ ...oldState, skills: oldState.skills.concat(skill) }))
    }

    const handleSubmit = async() =>{
        for(const field in jobDetails){
            if(typeof jobDetails[field] == 'string' && !jobDetails[field]) 
            return ;
        }
        if(!jobDetails.skills.length){
            return  
        }
        
        setLoading(true);
        await props.postJob(jobDetails);
        closeModal();
    }
    const skills = ["Javascript", "React.js", "Node.js", "Vue", 'firebase', "MongoDB","HTML", "CSS","Java"]
    const classes = useStyles();
    // console.log(jobDetails);

    return (

        <Dialog open={props.jobModal} fullWidth>
            <DialogTitle>
                <Box display='flex' justifyContent='space-between'>
                    Post Job
                    <IconButton onClick={closeModal}><CloseIcon /> </IconButton>
                </Box>
            </DialogTitle>
            <DialogContent>
                <Grid spacing={2} container>


                    <Grid item xs={6}>
                        <FilledInput onChange={handleChange} autoComplete='off' name="title" value={jobDetails.title} placeholder="Job title *" disableUnderline fullWidth />
                    </Grid>

                    <Grid item xs={6}>
                        <Select onChange={handleChange} name='type' value={jobDetails.type} fullWidth variant='filled' disableUnderline>
                            <MenuItem value='Full time'>Full time</MenuItem>
                            <MenuItem value='Part time'>Part time</MenuItem>
                            <MenuItem value=' Contract'> Contract</MenuItem>
                        </Select>
                    </Grid>

                    <Grid item xs={6}>
                        <FilledInput onChange={handleChange} autoComplete='off' name="companyName" value={jobDetails.companyName} placeholder="Company Name*" disableUnderline fullWidth />
                    </Grid>

                    <Grid item xs={6}>
                        <FilledInput onChange={handleChange} autoComplete='off' name="companyUrl" value={jobDetails.companyUrl} placeholder="Company Url*" disableUnderline fullWidth />
                    </Grid>


                    <Grid item xs={6}>
                        <Select name="location" onChange={handleChange} value={jobDetails.location} fullWidth variant='filled' disableUnderline>
                            <MenuItem value='Remote'>Remote</MenuItem>
                            <MenuItem value='On Site'>On Site</MenuItem>
                        </Select>
                    </Grid>

                    <Grid item xs={6}>
                        <FilledInput onChange={handleChange} autoComplete='off' name="link" value={jobDetails.link} placeholder="Job link*" disableUnderline fullWidth />
                    </Grid>

                    <Grid item xs={12}>
                        <FilledInput onChange={handleChange} autoComplete='off' name="description" value={jobDetails.description} placeholder="Job Description*" multiline rows={4} disableUnderline fullWidth />
                    </Grid>

                </Grid>

                <Box mt={2}>
                    <Typography>Skills*</Typography>
                    <Box display='flex'>
                        {skills.map(skill => <Box onClick={()=>addRemoveSkill(skill)} className={`${classes.skillChip} ${jobDetails.skills.includes(skill) && classes.included}`} key={skill}>{skill}</Box>)}
                    </Box>
                </Box>
            </DialogContent>
            <DialogActions>
                <Box width="100%" display="flex" color="red" justifyContent="space-between">
                    <Typography variant="caption">
                        *Required Fields
                    </Typography>
                    <Button disable={loading.toString()} variant="contained" disableElevation color="primary" onClick={handleSubmit}>
                        {loading ?<CircularProgress color='secondary' size={22}/>
                       : "Post Job"
                    }
                    </Button>
                </Box>
            </DialogActions>
        </Dialog>
    )
}