import React from "react";
import { Box, Grid, Typography, Button,makeStyles } from "@material-ui/core";



const useStyles = makeStyles({
  joinBtn: {
    '&:hover': {
      transition:'0.4s',
      backgroundColor: '#777',   
      padding:'5px',
      borderRadius:'10px'
  }
},
activate:{
  borderRadius:'10px',
   backgroundColor: '#555',
   padding:'5px',
   maxHeight:'34px'

 }
}

)

export default (props) =>{
  const classes = useStyles();
return(
  
<Box style={{position:"sticky", top:0, zIndex:4}} py={3} bgcolor='secondary.main' color='white'>
    <Grid container justify="center"> <Grid item xs={10}>
    <Box display='flex' justifyContent='space-between'>
      <Typography variant="h4">Open Job Listing</Typography>
      <Typography variant="h5" className={classes.joinBtn}>Home</Typography>
      <Typography variant="h5" className={classes.joinBtn}>About</Typography>
      <Typography variant="h5" className={`${classes.joinBtn } ${classes.activate}`}>Career</Typography>
      <Typography variant="h5" className={classes.joinBtn}>Services</Typography>
      <Typography variant="h5"className={classes.joinBtn}>Contact</Typography>
      <Button onClick={props.openJobModal} variant='contained' color='primary' disableElevation>Post a Job</Button>
    </Box>
    </Grid> </Grid>
</Box>
)}

